}

.arrow_link {
  color: #333;
  -webkit-backdrop-filter: blur(1px);
  backdrop-filter: blur(1px);
  padding-right: 13px;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.arrow_link:hover {
  color: #ea4781;
}

.arrow_link:hover:before {
  right: -43px;
}

.arrow_link:hover:after {
  right: -55px;
  transform: scale(1.2);
}

.arrow_link:before {
  content: "";
  width: 40px;
  height: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='8' viewBox='0 0 40 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M39.35 4.35a.5.5 0 000-.7L36.17.46a.5.5 0 10-.7.71L38.28 4l-2.83 2.83a.5.5 0 10.71.7l3.18-3.18zM0 4.5h39v-1H0v1z' fill='%23EA4781'/%3E%3C/svg%3E");
  background-position: center;
  background-repeat: no-repeat;
  padding: 6px 0;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 1px;
  right: -40px;
}

.arrow_link:after {
  content: "";
  height: 28px;
  width: 28px;
  background-image: url("data:image/svg+xml,%3Csvg width='28' height='28' viewBox='0 0 28 28' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='14' cy='14' r='13.5' stroke='%23EA4781'/%3E%3C/svg%3E");
  transition: all .3s;
  display: block;
  position: absolute;
  top: -3px;
  right: -52px;
}

